.widget {
  margin: 0 auto;
  padding: 40px;
  max-width: 900px;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  font-weight: 400;
}

.widget__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}


.widget__header .stars__item {
  -webkit-filter: invert(73%) sepia(55%) saturate(1%) hue-rotate(11deg) brightness(98%) contrast(99%);
          filter: invert(73%) sepia(55%) saturate(1%) hue-rotate(11deg) brightness(98%) contrast(99%);
}

.widget__header .rating {
  max-width: 250px;
}

.widget__write {
  width: 250px;
  border-radius: 20px;
  font-size: 18px;
  font-weight: 400;
  line-height: 45px;
  color: #ffffff;
  text-align: center;
  border: 1px solid #000000;
  background: #000000;
  transition: .3s;
}

.widget__write:hover {
  background: #ffffff;
  color: #000000;
}

@media (max-width: 579px) {
  .widget__write {
    min-width: 100%;
    margin-top: 10px;
  }
}

.rating {
  display: flex;
  flex-wrap: wrap;
}

.rating__value {
  font-size: 32px;
  color: #000000;
}

.recommends {
  font-size: 14px;
  font-weight: 400;
}

.stars {
  padding-left: 10px;
  display: flex;
  align-items: center;
  grid-gap: 5px;
  gap: 5px;
}

.stars__item {
  width: 18px;
  height: 18px;
  background-repeat: no-repeat;
  background-position: center center;
}

.stars__item_on {
  background-image: url(/static/media/starfull.2478c861.svg);
}

.stars__item_off {
  background-image: url(/static/media/emptystar.eada6b8c.svg);
}

.stars__item_half {
  background-image: url(/static/media/halfstar.3315a0aa.svg);
}
.slider {
  padding: 20px 0;
  display: flex;
  grid-gap: 20px;
  gap: 20px;
}

.slider__item {
  width: 150px;
  height: 150px;
}

.slider__image {
  width: 100%;
  height: 100%;
}
@media (max-width: 579px) {
  .slider__item {
    width: 100px;
    height: 100px;
  }
}

.filter-comments {
  padding: 20px 0;
  display: flex;
  align-items: center;
}

.filter-comments__title {
  padding-right: 10px;
  font-size: 18px;
  font-weight: 400;
}

.filter-comments__choice {
  width: 200px;
  padding: 5px;
  border-radius: 5px;
  font-size: 16px;
  font-weight: 400;
}

.filter-comments__choice option {
  font-size: 16px;
  font-weight: 400;
}
@media (max-width: 579px) {
  .filter-comments {
    flex-direction: column;
  }
  .filter-comments__title {
    display: none;
  }
}

.comment__item {
  padding: 20px 0;
  border-top: 1px solid #cccccc;
}

.comment .stars {
  padding: 0;
}

.comment__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.author__name {
  font-weight: 700;
}

.author__rating-detail {
  padding: 10px 0;
  display: flex;
  flex-wrap: wrap;
}

.author__detail {
  padding-left: 10px;
  font-size: 14px;
}

.comment__date {
  font-size: 14px;
  font-weight: 400;
}

.comment__btns {
  padding-top: 30px;
  display: flex;
  grid-gap: 5px;
  gap: 5px;
}

.comment__btn {
  width: 50px;
  height: 30px;
  border-radius: 5px;
  text-align: right;
  background-position: left;
  background-repeat: no-repeat;
  background-color: #ffffff;
  background-size: 30px 20px;
  transition: background-color .3s;
}

.comment__btn_like {
  background-image: url(/static/media/like.aa3677e9.svg);
}

.comment__btn_dislike {
  background-image: url(/static/media/dislike.be582e6a.svg);
}

.comment__btn_liked,
.comment__btn_liked {
  background-color: #e4e0e0;
}

.comment__btn:hover {
  background-color: #e4e0e0;
}

.comment__more {
  width: 100%;
  height: 50px;
  font-size: 18px;
  font-weight: 400;
  text-align: center;
  color: #777676;
  border: 1px solid #777676;
  border-radius: 20px;
}

.comment__more:hover {
  background-color: #797878;
  color: #ffffff;
}


@media (max-width: 579px) {
  .comment__header {
    padding-bottom: 10px;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
  }

  .author__rating-detail {
    padding: 5px 0;
    flex-direction: column;
  }

  .author__detail {
    padding: 5px 0 0;
    font-size: 13px;
  }

  .comment__review {
    text-align: justify;
  }
}

